#infoSection {
  pointer-events: none;
  z-index: 5;
  box-sizing: border-box;
  justify-content: center;
  gap: 60px;
  width: 100%;
  padding: 0 1rem;
  display: flex;
  position: absolute;
  top: 25px;
  left: 0;
}

.info-box {
  opacity: 0;
  color: #fff;
  box-sizing: border-box;
  background: #ffffff0d;
  border: 3px solid #fff;
  border-radius: 10px;
  flex: 45%;
  min-width: 280px;
  max-width: 550px;
  height: 600px;
  padding: 3rem;
  transition: opacity 4s;
  position: relative;
  overflow-y: auto;
}
